body {
    color: #000;
    background-color: #969696;
}

header {
    padding: 50px 0;
}

.firstScreenPage {
    position: relative; 

    width: 100vw;
    height: 100vh;

    background-image: url(../img/samolet_zdanie_fasad_125110_2560x1600.jpg);
    background-repeat: no-repeat;
    background-position: center;
}
.firstScreenPage h1 {
    position: absolute;
    top: 48%;

    padding: 20px 20px;

    color: #fff; 
    background-color: #5497a0;
}
.firstScreenPage .scrollDown {
    position: relative;
    top: 90%;

    width: 25px;
    height: 25px;
    margin: auto; 

    background-image: url(../img/down-arrow.svg);
}

.headerBackground {
    height: 150px;

    background-color: #5497a0;
    background-size: cover;
}
.headerBackground .headerTitle {
    padding-top: 40px;
}

.logoBackground {
    background-image: url(../img/logo.png);
    background-repeat: no-repeat;
    background-position: center;
}

main {
    padding: 20px 0;
    padding-bottom: 100px;
}

.portretBackground {
    height: 450px; 

    background-image: url(../img/portret.jpg);
    background-repeat: no-repeat;
    background-position: center;
}

nav {
    height: 100px;
    padding-top: 30px; 

    background-color: #8d8989;
}
nav a {
    margin: auto;

    font-size: 22px;
    line-height: 25px;

    color: #c9c9c9; 
    border: 1px solid black;
    border-radius: 10px;
}

footer {
    bottom: 0;
    left: 40%; 

    margin: 0 auto;
    padding: 15px;
}
footer div {
    margin: auto;
}

span span {
    font-weight: 600;
}
